import React from 'react';
import './WelcomeScreen.css';

export default function WelcomeScreen({ showWelcomeScreen, getAccessToken }) {
  return showWelcomeScreen ? (
    <div className="WelcomeScreen">
      <h1>Welcome to MeetApp</h1>
      <h4>
        Log in to see upcoming events around the world for full-stack
        developers.
      </h4>
      <div className="button_cont" align="center">
        <div class="google-btn">
          <div class="google-icon-wrapper">
            <img
              class="google-icon"
              src="https://upload.wikimedia.org/wikipedia/commons/5/53/Google_%22G%22_Logo.svg"
              alt="Google sign-in"
            />
          </div>
          <button
            onClick={() => getAccessToken()}
            rel="nofollow noopener"
            class="btn-text"
          >
            <b>Sign in with Google</b>
          </button>
        </div>
      </div>
      <a
        href="https://ryrojas.github.io/meetapp/privacy.html"
        rel="nofollow noopener"
      >
        Privacy Policy
      </a>
    </div>
  ) : null;
}
